import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home.vue";
import Layout from "@/components/include/Layout.vue";
import admin from "@/routers/admin";
import university from "@/routers/university";
// import SidebarAdmin from "@/components/include/sidebar/SidebarAdmin.vue";
import Sidebar from "@/components/include/sidebar/Sidebar.vue";
import ChildDefaultLayout from "@/components/include/ChildDefaultLayout.vue";
import evaluateRouteConfig from "./evaluate";
import inspectorRouter from './inspector';
Vue.use(Router);
const router = new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/",
            name: "home",
            component: Home,
        },
        {
            path: "/bck",
            component: Layout,
            children: [
                {
                    path: "admin",
                    components: {
                        sidebar: Sidebar,
                        default: ChildDefaultLayout,
                    },
                    children: admin,
                },
                {
                    path: "university/:id",
                    components: {
                        sidebar: Sidebar,
                        default: ChildDefaultLayout,
                    },
                    children: university,
                },
                {
                    path: "danh-gia/:id",
                    components: {
                        sidebar: Sidebar,
                        default: ChildDefaultLayout,
                    },
                    children: evaluateRouteConfig,
                },
                {
                    path: "kiem-dinh",
                    components: {
                        sidebar: Sidebar,
                        default: ChildDefaultLayout,
                    },
                    children: inspectorRouter,
                },
            ],
        },
        {
            path: "/dang-nhap",
            name: "login",
            component: () => import("@/views/LoginPage.vue"),
        },
        {
            path: "/about",
            name: "about",
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ "@/views/About.vue"),
        },
        {
            path: "/404",
            name: "notFound",
            component: () => import("@/views/NotFound.vue"),
        },
        {
            path: "*",
            redirect: "/404",
        },
    ],
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }
        else {
            return { x: 0, y: 0 };
        }
    },
});
router.beforeEach(async (to, from, next) => {
    // const role = to.meta.role;
    // store.dispatch('changeCollapse', false);
    // if (role && role.indexOf(store.state.user.role_id) === -1  ){
    //   router.push({name: 'home'})
    // }
    next();
});
export default router;
