const actions = {
    async init({ dispatch, rootState }) {
        try {
            const checkToken = sessionStorage.getItem('access_token');
            if (!checkToken) {
                return Promise.resolve(false);
            }
            dispatch('user/updateToken', checkToken, { root: true });
            if (!rootState.user.fullname || rootState.user.fullname !== '') {
                await dispatch('user/getUserInfo', {}, { root: true });
            }
            return Promise.resolve(true);
        }
        catch (error) {
            return Promise.resolve(false);
        }
    },
    async resetData({ dispatch }) {
        // await dispatch('user/getUserInfo', {}, {root: true})
        // dispatch('user/updateToken', '', { root: true })
        sessionStorage.clear();
    }
};
export default actions;
