import { assign } from 'lodash';
import request from '@/plugins/request';
const actions = {
    async insertUniversity({ commit, dispatch }, uniId) {
        try {
            const data = await request.get(`university/${uniId}`);
            request.interceptors.request.use(request => {
                request.params = assign(request.params, {
                    university_id: uniId
                });
                return request;
            });
            commit('UNI_INSERT', data.data.data.university);
            console.log(data);
            return Promise.resolve();
        }
        catch (error) {
            return Promise.reject(error);
        }
    },
    clearUniversityData({ commit }) {
        commit('SET_UNI_DATA', {});
    }
};
export default actions;
