import axios from 'axios';
import { API_URL } from "@/config";
import { Message } from 'element-ui';
import { includes } from 'lodash';
console.log(API_URL);
const request = axios.create({
    baseURL: API_URL,
    withCredentials: false
});
request.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded';
request.defaults.headers.common['Authorization'] =
    (sessionStorage.getItem('access_token') !== null) ? ('Bearer ' + sessionStorage.getItem('access_token')) : '';
// request.interceptors.request.use(
//   request => {
//     request.params = assign(request.params, {
//     })
//     return request
//   }
// )
request.interceptors.response.use(response => {
    return response;
}, error => {
    if (!error.response || error.response.status >= 500) {
        error.message = 'Không thể kết nối đến máy chủ';
        Message.error(error.message);
        return Promise.reject(error);
    }
    if (error.response.status < 500) {
        console.log(error.response.data);
        if (error.response.status === 401) {
            Message.error('Lỗi đăng nhập !');
            setTimeout(() => {
                console.log('local', location.href);
                if (!includes(location.href, 'dang-nhap')) {
                    location.href = '/dang-nhap';
                }
            }, 200);
            return Promise.reject(error);
        }
        else if (error.response.status === 400) {
            if (!error.response.data.message) {
                const listErrors = Object.keys(error.response.data).map((key) => {
                    return error.response.data[key];
                });
                listErrors.forEach(value => {
                    value.forEach((err) => {
                        setTimeout(() => {
                            Message.error(err);
                        }, 100);
                    });
                });
            }
            else {
                Message.error(error.response.data.message);
            }
        }
        return Promise.reject(error);
    }
    if (error.response.status === 500) {
        Message.error('Chức năng này đang được bảo trì, chúng tôi sẽ thông báo khi bảo trì hoàn thành');
        // router.push({ name: 'login' });
        // sessionStorage.clear()
    }
    return Promise.reject(error);
});
export default request;
